import Vue from 'vue';
import Router from 'vue-router';
import Collection from './views/collection/collection.vue';
import Receive from './views/receive/receive.vue';
import Success from './views/success/success.vue';
import Agreement from './views/agreement/agreement.vue';
Vue.use(Router);
export default new Router({
    routes: [
        {
            path: '/',
            name: 'collection',
            component: Collection
        },
        {
            path: '/receive',
            name: 'receive',
            component: Receive
        },
        {
            path: '/success',
            name: 'success',
            component: Success
        },
        {
            path: '/agreement',
            name: 'agreement',
            component: Agreement
        }
    ]
});
