import { __decorate } from "tslib";
import { Component, Vue } from "vue-property-decorator";
import { namespace } from "vuex-class";
import { lan } from "@/utils/bower.ts";
const Common = namespace("common");
let Receive = class Receive extends Vue {
    constructor() {
        super(...arguments);
        this.img = '';
    }
    mounted() {
        document.title = '邀请奖励';
        if (lan() == "cn") {
            this.img = require('../../assets/pic_cn.png');
        }
        else {
            this.img = require('../../assets/pic_en.png');
        }
    }
    downloadClick() {
        window.open('https://bitpie.com', '_self');
    }
};
Receive = __decorate([
    Component
], Receive);
export default Receive;
