var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"agreement-div"},[_c('div',{staticClass:"agreement-content"},[_c('h1',[_vm._v("币币C2C借贷协议")]),_c('p',{staticClass:"content"},[_vm._v("Bitpie 的 C2C 借贷，是一个提供数字资产进行质押借贷的专区，为有资金需求和投资需求的数字资产持有者提供安全、高效、便捷的需求匹配服务和托管服务。在本协议中，Bitpie 称为“平台”；借入资产者称为“借款人”；投资人称为“出借人”。")]),_c('p',{staticClass:"content"},[_vm._v("\n      借款人\n      "),_c('strong',[_vm._v("  Bitpie UID")])]),_c('p',{staticClass:"content"},[_vm._v("\n      出借人\n      "),_c('strong',[_vm._v("  Bitpie UID")])]),_c('p',{staticClass:"content"},[_vm._v("\n      借款本金金额\n      "),_c('strong',[_vm._v("  （“借款金额”）")])]),_c('p',{staticClass:"content"},[_vm._v("\n      借款利息金额\n      "),_c('strong',[_vm._v("   (“利息金额”）")])]),_c('p',{staticClass:"content"},[_vm._v("\n      借款利率\n      "),_c('strong',[_vm._v("   % /年")])]),_c('p',{staticClass:"content"},[_vm._v("\n      借款放款日\n      "),_c('strong',[_vm._v("  最终到期日")])]),_c('p',{staticClass:"content"},[_vm._v("\n      起息日\n      "),_c('strong',[_vm._v("  还款方式  一次性还本付息")])]),_c('p',{staticClass:"content"},[_vm._v("\n      服务费\n      "),_c('strong',[_vm._v("  （由出借人支付，金额为应收利息的15%。）")])]),_c('p',{staticClass:"mark top-offset bottom-offset"},[_vm._v("注：借款人及出借人委托平台对相关金额进行计算，并在平台 APP发布或更新具体的还款明细。上述还款明细中列明的具体还款本息金额若与平台发布或更新的还款明细不一致的，以平台在APP内发布或更新的还款明细为准")]),_c('p',{staticClass:"title bottom-offset"},[_vm._v("一、协议约定")]),_c('p',{staticClass:"content"},[_vm._v("1、借款人指质押相应价值的数字资产托管在平台，借入投资人资金的一方。")]),_c('p',{staticClass:"content"},[_vm._v("2、出借人指投资人，即提供数字资产借出并收取利息的一方。")]),_c('p',{staticClass:"content"},[_vm._v("3、质押物指在平台进行托管质押，作为所借资金担保物的数字资产。")]),_c('p',{staticClass:"content"},[_vm._v("4、服务费指出借人通过在平台进行出借获得利息报酬，平台向其收取的费用。")]),_c('p',{staticClass:"content"},[_vm._v("5、借款利率是指订单列明中的借款利率比例，以年利率的方式表示。年利率=日利率*365。")]),_c('p',{staticClass:"content"},[_vm._v("6、利息指出借人因借出资产而从借款人手中获得的报酬。")]),_c('p',{staticClass:"content"},[_vm._v("7、还款日是指借贷订单中显示的借款人应支付应付款项的日期。")]),_c('p',{staticClass:"content"},[_vm._v("8、质押率指借款金额比质押币的市值的比率。")]),_c('p',{staticClass:"content"},[_vm._v("9、资金及借款还款中的款均指相应币种的数字资产。")]),_c('p',{staticClass:"title top-offset bottom-offset"},[_vm._v("二、借款与还款")]),_c('p',{staticClass:"content"},[_vm._v("1、出借人和借款人可以在借贷专区发布出借资金或借款信息的广告。另一方可以选择市场挂单中选择借入或借出。")]),_c('p',{staticClass:"content"},[_vm._v("2、出借人需要在派银行持有相应出借币种的数字资产，所需资产量由订单金额计算得出。")]),_c('p',{staticClass:"content"},[_vm._v("3、借款人需要在派银行持有相应质押币种数字资产用于质押作为担保物，所需质押量由订单金额计算得出。")]),_c('p',{staticClass:"content"},[_vm._v("4、出借人及借款人委托平台对数字资产币种的市场价值进行实时监测并用于计算质押量、质押率，平台有权根据市场情况选择不同来源的市场价格进行市场价值综合计算，出借人及借款人均认可平台所用来计算市场价值的市场价格。")]),_c('p',{staticClass:"content"},[_vm._v("5、出借人可以发布出借广告，出借人派银行中资产量达到广告订单所需支付利息时即可发布广告，发布后广告订单所需支付利息等额资产会被作为保证金划转至平台，当订单匹配后，出借人需在24小时内在派银行补足全额出借金额，出借人补足出借资金后订单成交，出借资金将被划转并发放给借款人，保证金将被退回派银行，同时服务费将被从派银行中扣除。出借人未按规定时间全额补足出借金额会导致订单成交失败，出借人用于当前订单的保证金将被扣除补偿借款人及平台，不再退还给出借人，借款人获得保证金总额85%的补偿，平台获得保证金总额15%的补偿。")]),_c('p',{staticClass:"content"},[_vm._v("6、出借人可在市场挂单中选择借出。当订单匹配时，出借人派银行中相应数量的数字资产将以及所需支付的服务费被划转至平台。如借款人尚未进行全额质押，借款人有24小时用来补足质押物，当质押物补足后，订单成交，出借资金将并发放给借款人。如借款人未按时补足，订单成交失败，出借资金及服务费将被划转回出借人派银行账户，同时出借人及平台可获得借款人的保证金作为补偿，出借人获得保证金总额85%的补偿，平台获得保证金总额15%的补偿。")]),_c('p',{staticClass:"content"},[_vm._v("7、借款人可以发布借款广告，借款人派银行中持有对应币种的资产市场价值达到订单所需支付利息时即可发布广告，发布后广告订单所需支付利息对应的等值资产会被作为保证金划转至平台，当订单被匹配后借款人需在24小时内补足所需质押资产至派银行账户。当质押资金补足后，订单成交，质押物将被划转至平台托管，已划转保证金也将自动转为质押物合并计算，同时借款将被发放给借款人。如借款人未按规定时间补足质押资产，订单成交失败，已被划转的保证金将作为罚金补偿出借人及平台，不再退还借款人，出借人获得保证金总额85%的补偿，平台获得保证金总额15%的补偿。")]),_c('p',{staticClass:"content"},[_vm._v("8、 借款人可在市场挂单中选择借入。当订单匹配时，借款人相应数量的质押资产会被划转至平台账户托管。如出借人的借出款尚未进行全额托管，出借人有24小时用来补足借出款，补足后借出款将被划转发放给借款人。如出借人未按规定时间补足借出款、质押资产将被返回给借款人，同时借款人可获得出借人的订单保证金作为补偿。如订单匹配时出借人的借出款已进行全额托管，将被发放给借款人。")]),_c('p',{staticClass:"content"},[_vm._v("9、放款成功后次日开始计息即起息日(GMT+8时区)。")]),_c('p',{staticClass:"content"},[_vm._v("10、借款人需要根据平台最低质押率质押数字资产，如果因行情变化质押资产市场价值降低，需要及时补充质押物；并且于到期日按时还款。")]),_c('p',{staticClass:"content"},[_vm._v("11、借款人有义务按时足额向出借人支付本金和利息。还款时借款人需将约定的本金及利息转入派银行，系统自动扣款。如果提前还款，借款人仍需按照本协议向出借人