"content"},[_vm._v("4）借款人不得使用借入资金用于法律明令禁止或限制的经营活动。")]),_c('p',{staticClass:"content"},[_vm._v("5）借款人与出借人承诺：不会使用平台进行信用卡套现，洗钱，非法集资，金融诈骗等其他不正当交易行为。")]),_c('p',{staticClass:"content"},[_vm._v("6）身份证信息，手机号码，邮箱，银行账户变更等，需要在 2 个工作日之内联系平台客服进行更正，因本人变更信息导致通知送达不到的，因此产生的纠纷或争议，由本人承担。")]),_c('p',{staticClass:"subtitle"},[_vm._v("2、限制条件")]),_c('p',{staticClass:"content"},[_vm._v("1）出借人及借款人使用本平台时，必须遵守本地区法律规定，在当地法律受限范围公民禁止使用该服务。")]),_c('p',{staticClass:"content"},[_vm._v("2）禁止未成年人使用平台账户进行操作。")]),_c('p',{staticClass:"title top-offset bottom-offset"},[_vm._v("七、保密及违约责任")]),_c('p',{staticClass:"content"},[_vm._v("1、借贷双方不得泄露订单信息，因借贷业务获取的对方信息，仅用于该订单处理，不得做其他用途，如若违反，平台及对方有权追究法律责任。")]),_c('p',{staticClass:"content"},[_vm._v("2、保护本人账户安全并保护对方隐私，不得使用对方信息从事非法活动；若因泄露对方信息造成损害后果的，平台支持对方进行司法救济。")]),_c('p',{staticClass:"content"},[_vm._v("3、出借人及借款人应采取所有其他必要、适当和可以采取的措施，以确保保密信息的保密性。")]),_c('p',{staticClass:"content"},[_vm._v("4、对于在借贷过程中产生的纠纷，平台有最终裁判权。")]),_c('p',{staticClass:"content"},[_vm._v("5、对于恶意拖欠还款资金，伪造证据，涉嫌非法资金来源，洗钱，诈骗等行为，一经核实将冻结相关账户，必要时协助司法基本进行处理。")]),_c('p',{staticClass:"content"},[_vm._v("6、由于借出资金归属，合法性存在争议的，由出借人负责解决并自行承担相关责任，与平台无关。")]),_c('p',{staticClass:"content"},[_vm._v("7、借款人和出借人授权和委托平台根据本协议所采取的全部行动和措施的法律后果，均归属于借款人和出借人本人；在任何情形下，平台不是本协议项下任何借款或债务的债务人或需要以其自有资金偿还本协议项下的任何借款或债务。平台亦不是本协议项下质押的借款人或需要以其自有资金承担本协议项下的质押责任，且对质押物的价值以及借款人的还款能力不负任何担保责任。")]),_c('p',{staticClass:"title top-offset bottom-offset"},[_vm._v("八、其他")]),_c('p',{staticClass:"content"},[_vm._v("本协议的签订地为塞舌尔共和国，协议的履行、终止、解释均适用塞舌尔共和国法律。双方同意，因本协议所产生的或与本协议有关的一切争议，双方应协商解决；协商无法达成一致的，双方将争议提交平台进行调解；平台无法协调争议的，双方均可发起诉讼。")]),_c('p',{staticClass:"content bottom-page"},[_vm._v("在使用 C2C 借贷服务前，请仔细阅读该协议，一旦进行相关操作，视为您已经阅读并同意该条款之规定。平台有权修改协议内容，无须另行通知。对于该协议内容，平台有最终解释权。")])])])}]

